body {
  box-sizing: border-box;
  margin: 0;
  font-family: "Karla", sans-serif;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  background-image: linear-gradient(90deg,#672280, #A626D3);
  color: #fff;
}

nav .logo {
  display: flex;
  align-items: center;
}

nav .logo--img {
  height: 3rem;
  margin-right: 0.8rem;
}

nav .logo--text {
  font-size: 1.7rem;
}

nav .course-title {
  font-size: 1.3rem;
  font-weight: normal;
}

main {
  max-width: 768px;
  margin: auto;
  padding: 2rem;
}

.form {
  display: grid;
  grid-template: 50px 50px / 1fr 1fr;
  gap: 18px
}

.form--button {
  grid-column: 1 / -1;
  font-family: "Karla", sans-serif;
  border-radius: 6px;
  border: none;
  background-image: linear-gradient(90deg,#672280, #A626D3);
  color: #fff;
  font-size: 1.2rem;
  cursor: pointer;
}

.form--input {
  font-family: "Karla", sans-serif;
  border-radius: 6px;
  border: 1px solid #D5D4D8;
  text-indent: 16px;
  font-size: 1.2rem;
}

.meme {
  position: relative;
}

.meme--img {
  max-width: 100%;
  width: 100%;
  margin-top: 2em;
}

.meme--text {
  position: absolute;
  width: 80%;
  text-align: center;
  left: 50%;
  transform: translateX(-50%);
  margin: 40px 0;
  padding: 0 5px;
  font-family: impact, sans-serif;
  font-size: 2em;
  text-transform: uppercase;
  color: white;
  letter-spacing: 1px;
  text-shadow:
      2px 2px 0 #000,
      -2px -2px 0 #000,
      2px -2px 0 #000,
      -2px 2px 0 #000,
      0 2px 0 #000,
      2px 0 0 #000,
      0 -2px 0 #000,
      -2px 0 0 #000,
      2px 2px 5px #000;
}

.bottom {
  bottom: 0.1em;
}

.top {
  top: 1em;
}